import React, { Component } from "react"
import "./styles/Cheque.css"
class Cheque extends Component {
    render() {
        return (
            <div className="container cheque p-3 bg-dark">
                <div className="row d-flex align-items-center">
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Fecha Del Dia"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Entrada)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Cliente"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Cliente)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Banco"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Banco)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Numero"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Numero)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>{this.props.cheque["Fecha Del Cheque"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Fecha)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    <h6>$ {this.props.cheque["Monto"]}</h6>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Monto)</small>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    {this.props.cheque["Fecha De Salida"] ?
                                        <h6>{this.props.cheque["Fecha De Salida"]}</h6>
                                        :
                                        <h6 className="salida" onClick={
                                            e => {
                                                console.log(this.props.key)
                                            }
                                        }>-</h6>
                                    }
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Salida)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col text-center">
                        <div className="container">
                            <div className="row">
                                <div className="col text-center">
                                    {this.props.cheque["Enviado A"] ?
                                        <h6>{this.props.cheque["Enviado A"]}</h6>
                                        :
                                        <h6>-</h6>
                                    }
                                </div>
                            </div>
                            <div className="row">
                                <div className="col text-center">
                                    <small>(Enviado A)</small>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default Cheque